import React from 'react';
import Bootcamp from './Bootcamp.js';
import AddBootCampModal from './AddBootCampModal.js';
import save from '../Menu/create.svg';
import './MainBootcamp.css';

export default class MainBootcamp extends React.Component {

	state = {
		bootCampModal: false
	}

	handleAddBootCamp = () => {
		this.setState({bootCampModal: true})
	}
	modalAddBootCampModalClose = () => {
		this.setState({bootCampModal: false})
	}

	render() {
		return (
			<div className="dayboard">
				{this.state.bootCampModal && <AddBootCampModal modal={this.modalAddBootCampModalClose} /> }
				<span>Bootcamp 3</span>
				<div className="topic-data">
					<span>Выбор потока</span>
					<select>
						<option>Bootcamp 1</option>
						<option>Bootcamp 2</option>
						<option>Bootcamp 3</option>
					</select>
					<img className="nav-icons main-bootcamp" src={save} alt="save" onClick={this.handleAddBootCamp} />
				</div>
				<p>Учасники</p>
				<Bootcamp />
			</div>
		)
	}
}


