import React from 'react';
import './menu.css';
import createIcon from './create.svg'
import score from './score.svg'
import exit from './exit.svg'
import list from './list.svg'
import { withRouter } from 'react-router-dom';

class Menu extends React.Component {
    constructor() {
        super();
        this.state = {openDays: false, openCamps: false}
    }

    handleClick = (href) => {
        this.props.history.push(href);
    }

    toggleDays = () => {
        this.setState({openDays: !this.state.openDays})
    }
    toggleCamps = () => {
        this.setState({openCamps: !this.state.openCamps})
    }
    render() {
        return (
            <nav>
                <ul className="dashboard-menu">
                    <li onClick={this.handleClick.bind(null, '/dashboard/create-camp')}>
                        <img className="nav-icons" src={createIcon} alt="create" />
                        <p>Создание потока</p>
                    </li>
                    <li onClick={this.handleClick.bind(null, '/dashboard/day')}>
                        <img className="nav-icons" src={list} alt="list" />
                        <p onClick={this.toggleCamps}>Выбор потока</p>
                    </li>
                    {this.state.openCamps ? 
                    (<ul className="days-menu">
                            <li>Bootcamp 5</li>
                            <li>Bootcamp 6</li>
                            <li>Bootcamp 7</li>
                        </ul>):
                        null
                    }
                    <li onClick={this.handleClick.bind(null, '/dashboard/day')}>
                        <img  className="nav-icons" src={score} alt="score"/>
                        <p onClick={this.toggleDays}>Данные за день</p>
                       
                    </li>
                    {this.state.openDays ? 
                    (<ul className="days-menu">
                            <li>День 1 - 10.01.17</li>
                            <li>День 2 - 11.01.17</li>
                            <li>День 3 - 12.01.17</li>
                            <li>Итоговые данные</li>

                        </ul>):
                        null
                    }
                    <li onClick={this.handleClick.bind(null, '/login')}>
                        <img  className="nav-icons" src={exit} alt="logout"/>
                        <p>Выход</p>
                    </li>
                </ul>
            </nav>
        )
    }
}

export default withRouter(Menu)


