import React from 'react';
import save from '../DayBoard/save.svg';
import SkillsModal from './SkillsModal.js';
import UsersModal from './UsersModal.js';
import '../DayBoard/dayboard.css';

export default class Bootcamp extends React.Component {

    state = {
        skillModal: false,
        userModal: false,
        students: ['Виктория М.']
    }

    modalSkillClose = () => {
        this.setState({skillModal : false})
    }
    modalUsersClose = () => {
        this.setState({userModal : false})
    }

    handleAddSkills = () => {
        this.setState({skillModal : true});
    }

    handleAddUser = () => {
        this.setState({userModal : true});
    }

    addUser = (user) => {
        if (user)this.setState({ students: [...this.state.students, user]}) 
    }

    render() {
        return (
            <table>
                {this.state.skillModal && <SkillsModal modal={this.modalSkillClose} /> }
                {this.state.userModal && <UsersModal modal={this.modalUsersClose} addUser={this.addUser} /> }
                <thead>
                    <tr>
                        <th className="first-column">№</th>
                        <th>Участник</th>
                        <th>Аватарка</th>
                        <th>Фото/Видео</th>
                        <th>Компания</th>
                        <th>Описание</th>
                        <th>Лого</th>
                        <th>FB</th>
                        <th>Skills</th>
                        <th>Cохранить</th>
                    </tr>
                </thead>
                <tbody>
                    {this.state.students.map((item, index) => <tr key={index}>
                        <td className="first-column">{index + 1}</td>
                        <td>{item}</td>
                        <td>
                            <label htmlFor="bootcamp-avatar">
                                <img className="bootcamp-add" src="../img/upload.png" alt="add"/>
                                <input id="bootcamp-avatar" className="bootcamp-file" type="file" accept=".jpg" ref={(ref) => this.fileUpload = ref} onChange={value => this.uploadFile()} /> 
                            </label>
                        </td>
                        <td>
                            <label htmlFor="bootcamp-avatar">
                                <img className="bootcamp-add" src="../img/upload.png" alt="add"/>
                                <input id="bootcamp-avatar" className="bootcamp-file" type="file" accept=".jpg" ref={(ref) => this.fileUpload = ref} onChange={value => this.uploadFile()} /> 
                            </label>
                        </td>
                        <td>
                            <input type="text" />
                        </td>
                        <td>
                            <input type="text" />
                        </td>
                        <td>
                            <label htmlFor="bootcamp-avatar">
                                <img className="bootcamp-add" src="../img/upload.png" alt="add"/>
                                <input id="bootcamp-avatar" className="bootcamp-file" type="file" accept=".jpg" ref={(ref) => this.fileUpload = ref} onChange={value => this.uploadFile()} /> 
                            </label>
                        </td>
                        <td>
                            <label htmlFor="bootcamp-avatar">
                                <img className="bootcamp-add" src="../img/upload.png" alt="add"/>
                                <input id="bootcamp-avatar" className="bootcamp-file" type="file" accept=".jpg" ref={(ref) => this.fileUpload = ref} onChange={value => this.uploadFile()} /> 
                            </label>
                        </td>
                        <td>
                            <img className="bootcamp-add" src="../img/add.png" alt="add" onClick={this.handleAddSkills}/>
                        </td>
                        <td>
                            <img className="nav-icons" src={save} alt="save" />
                        </td>
                    </tr>)}
                    <tr>
                        <td>
                            <img className="bootcamp-add" src="../img/add.png" alt="add" onClick={this.handleAddUser}/>
                        </td>
                    </tr>
                </tbody>     
            </table>
        )
    }
}


