import React from 'react';
import Modal from 'react-modal';
import './SkillModal.css';

const customStyles = {

	content: {
		top: '50%',
		left: '50%',
		right: 'auto',
		bottom: 'auto',
		marginRight: '-50%',
		transform: 'translate(-50%, -50%)',
		display: 'flex',
		flexDirection: 'column',
		padding: '20px',
		backgroundColor: '#ccf1f7'
	}
};
Modal.setAppElement('#root');


export default class AddBootCampModal extends React.Component {

	state = { modalIsOpen: true };

	openModal = () => {
		this.setState({ modalIsOpen: true });
	}

	afterOpenModal = () => {
		this.subtitle.style.color = '#09243b';
	}

	handleSave = () => {
		this.setState({ modalIsOpen: false });
		this.props.modal();
	}
	
	render() {
		return (
			<Modal
				isOpen={this.state.modalIsOpen}
				onAfterOpen={this.afterOpenModal}
				onRequestClose={this.closeModal}
				style={customStyles}
				contentLabel="No Overlay Click Modal"
			>
				<div id="modal" ref={subtitle => this.subtitle = subtitle}>BootCamp name</div>
				<div className="skillmodal-inputs">
					<input type="text" name="newUser" onChange={this.handleChange} />
					<br/>
						<span>Days: </span><input type="number" placeholder="21" step="1" min="15" max="25" />
					<br/>
				</div>
				<div className="modalButtonWrap">
					<button className="modalEndGameButton" onClick={this.handleSave}>Creat Bootcamp</button>
				</div>
			</Modal>
		)
	}
}



