import React from 'react';
import { withRouter } from 'react-router-dom';
import './login.css';

class Login extends React.Component {

	handleOnSubmit = (e) => {
		e.preventDefault();
	}

	handleButtonClick = () => {
		this.props.history.push("/dashboard")
	}


	render() {
		return (
			<div className="login-page">
				<div className="login-block">
					<div className="login-img-wrap">
						<img className="login-img" src="./img/lock-icon.png" />
					</div>
					<form onSubmit={this.handleOnSubmit}>
						<p className="login-text">Please login</p>
						<div className="login-wrap">
							<div className="login-input-wrap">
								<lable className="login-lable" htmlFor="username">Username</lable>
								<input className="login-input" id="username" type="text" name="login" />
								<hr className="login-hr" />
							</div>
						</div>
						<div className="login-wrap">
							<div className="login-input-wrap">
								<lable className="login-lable" htmlFor="password">Password</lable>
								<input className="login-input" id="password" type="text" name="password" />
								<hr className="login-hr" />
							</div>
						</div>
						<div className="button-wrap">
							<button className="login-button" onClick={this.handleButtonClick}>SIGN IN</button>
						</div>
					</form>
				</div>
			</div>
		)
	}
}
export default withRouter(Login);


