import React from 'react';
import save from './save.svg'

let students = ['Виктория М.', 'Сергей Б.', 'Артур К.', 'Мария С.', 'Павел Ш.', 'Юлия М.', 'Наталья Б.', 'Антон Р.', 'Михаил С.', 'Дмитрий К.', 'Виктор Щ.', 'Дарья Ф.', 'Анастасия В.']

export default class Dayboard extends React.Component {
    render() {
        return (
            <table>
                <thead>
                    <tr>
                        <th className="first-column">№</th>
                        <th>Участник</th>
                        <th>Страйк</th>
                        <th>ДЗ (1б)</th>
                        <th>Ежедневные правила (1б)</th>
                        <th>Заполнение чек-листа по каждой теме (1б)</th>
                        <th>Спикерский балл (1б)</th>
                        <th>Баллы от участников</th>
                        <th>Х-фактор (100б)</th>
                        <th>Отчет FB</th>
                        <th>Общий балл за день</th>
                        <th>Сохранить</th>
                    </tr>
                </thead>
                <tbody>
                    {students.map((item, index) => <tr key={index}>
                        <td className="first-column">{index + 1}</td>
                        <td>{item}</td>
                        <td>
                            <input type="checkbox" />
                        </td>
                        <td>
                            <input type="checkbox" />
                        </td>
                        <td>
                            <input type="checkbox" />
                        </td>
                        <td>
                            <input type="checkbox" />
                        </td>
                        <td> <input type="checkbox" /></td>
                        <td>
                            <input type="number" placeholder="0" step="1" min="0" max="5" />
                        </td>
                        <td>
                            <input type="checkbox" />
                        </td>
                        <td>
                            <input type="text" />
                        </td>
                        <td>
                            15
                        </td>
                        <td>
                            <img className="nav-icons" src={save} alt="save" />
                        </td>
                    </tr>)}
                </tbody>
            </table>
        )
    }
}


