import React from 'react';
import Modal from 'react-modal';
import './SkillModal.css';

const customStyles = {

	content: {
		top: '50%',
		left: '50%',
		right: 'auto',
		bottom: 'auto',
		marginRight: '-50%',
		transform: 'translate(-50%, -50%)',
		display: 'flex',
		flexDirection: 'column',
		padding: '20px',
		backgroundColor: '#ccf1f7'
	}
};
Modal.setAppElement('#root');


export default class UsersModal extends React.Component {

	state = { 
		modalIsOpen: true,
		newUser : ''
			 
	};

	openModal = () => {
		this.setState({ modalIsOpen: true });
	}

	afterOpenModal = () => {
		this.subtitle.style.color = '#09243b';
	}

	handleSave = () => {
		this.props.addUser(this.state.newUser);
		this.props.modal();
		this.setState({ modalIsOpen: false });
	}

	handleChange = (e) => {
		this.setState({
			[e.target.name] : e.target.value
		})
	}
	
	render() {
		return (
			<Modal
				isOpen={this.state.modalIsOpen}
				onAfterOpen={this.afterOpenModal}
				onRequestClose={this.closeModal}
				style={customStyles}
				contentLabel="No Overlay Click Modal"
			>
				<div id="modal" ref={subtitle => this.subtitle = subtitle}>User Name</div>
				<div className="skillmodal-inputs">
					<input type="text" name="newUser" onChange={this.handleChange} />
				</div>
				<div className="modalButtonWrap">
					<button className="modalEndGameButton" onClick={this.handleSave}>Add user</button>
				</div>
			</Modal>
		)
	}
}



