import React from 'react';
import Fines from './Fines';
import Scores from './Scores';

export default class Dayboard extends React.Component {
    render() {
        return (

            <div className="dayboard">
                <span>Bootcamp 5</span>
                <span>День 1 - 10.01.2017</span>
                <div className="topic-data">
                    <span>Спикер</span>
                    <select>
                        <option>Юнона Лотоцкая</option>
                        <option>Емченко Наталья</option>
                        <option>Андрей Пивоваров</option>
                    </select>
                    <span>Тема</span>
                    <select>
                        <option>EQ, стратегия, внедрение и личная эффективность</option>
                        <option>MVV</option>
                        <option>Лидерство</option>
                    </select>
                </div>
                <p>Баллы</p>
                <Scores />
                <hr />
                <p>Штрафы</p>
                <Fines />
            </div>
        )
    }
}


