import React from 'react';
import './dayboard.css';
import save from './save.svg'

let students = ['Виктория М.', 'Сергей Б.', 'Артур К.', 'Мария С.', 'Павел Ш.', 'Юлия М.', 'Наталья Б.', 'Антон Р.', 'Михаил С.', 'Дмитрий К.', 'Виктор Щ.', 'Дарья Ф.', 'Анастасия В.']

export default class Fines extends React.Component {

    render() {
        return (
            <table>
                <thead>
                    <tr>
                        <th>№</th>
                        <th>Участник</th>
                        <th>Нарушение правила</th>
                        <th>Сумма штрафа за нарушение</th>
                        <th>Опоздание в минутах</th>
                        <th>Сумма штрафа за опоздание</th>
                        <th>Общая сумма штрафов</th>
                        <th>Сохранить</th>
                    </tr>
                </thead>
                <tbody>
                    {students.map((item, index) => <tr key={index}>
                        <td>{index + 1}</td>
                        <td>{item}</td>
                        <td>
                            <select>
                                <option>без нарушений</option>
                                <option>пользование мобильным телефоном</option>
                                <option>что-то еще</option>
                            </select>
                        </td>
                        <td>0</td>
                        <td>
                        <input type="number" placeholder="0" step="1" min="0" max="100" />
                        </td>
                        <td>200</td>
                        <td>400</td>
                        <td>
                            <img className="nav-icons" src={save} alt="save" />
                        </td>
                    </tr>)}
                </tbody>
            </table>

        )
    }
}


