import React from 'react';
import './header.css';
import burgerMenu from './burgerMenu.svg'

export default class Header extends React.Component {
    render() {
        return (
            <header>
                <img src={burgerMenu} alt="burger-menu"/>
                <h3>Панель управления</h3>
            </header>
        )
    }
}


